import bottomTabBar from "@/components/bottomTabBar/bottomTabBar.vue"
import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue"
import { mapState, mapMutations } from 'vuex';
export default {
	components: {uniNavBar,bottomTabBar},
	data() {
		return {
			sumPrice: '0.00',
			selectedList: [], //被选中列表
			isAllselected: false, //都被选中
			//控制滑动效果
			theIndex: null,
			oldIndex: null,
			isStop: false,
		};
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	onLoad() {
		//兼容H5下结算条位置







		this.goodsList = this.cart
	},
	computed: mapState(['cart']),
	methods: {
		...mapMutations(['delShoppingCart','clearShoppingCart']),
		//加入商品 参数 goods:商品数据
		joinGoods(goods) {
			/*
			 * 这里只是展示一种添加逻辑，模板并没有做从其他页面加入商品到购物车的具体动作，
			 * 在实际应用上，前端并不会直接插入记录到goodsList这一个动作，一般是更新列表和本地列表缓存。
			 * 一般商城购物车的增删改动作是由后端来完成的,
			 * 后端记录后返回前端更新前端缓存
			 */
			let len = this.goodsList.length;
			let isFind = false; //是否找到ID一样的商品
			for (let i = 0; i < len; i++) {
				let row = this.goodsList[i];
				if (row.id == goods.id) {
					//找到商品一样的商品
					this.goodsList[i].number++; //数量自增
					isFind = true; //找到一样的商品
					break; //跳出循环
				}
			}
			if (!isFind) {
				//没有找到一样的商品，新增一行到购物车商品列表头部
				this.goodsList[i].unshift(goods);
			}
		},
		//控制左滑删除效果-begin
		touchStart(index, event) {
			//多点触控不触发
			if (event.touches.length > 1) {
				this.isStop = true;
				return;
			}
			console.log("theIndex",this.theIndex);
			this.oldIndex = this.theIndex;
			this.theIndex = null;
			console.log("theIndex",this.theIndex);
			//初始坐标
			this.initXY = [event.touches[0].pageX, event.touches[0].pageY];
			console.log("this.initXY",this.initXY);
		},
		touchMove(index, event) {
			//多点触控不触发
			if (event.touches.length > 1) {
				this.isStop = true;
				return;
			}
			let moveX = event.touches[0].pageX - this.initXY[0];
			let moveY = event.touches[0].pageY - this.initXY[1];
			
			console.log("moveX",moveX,"moveY",moveY);
			if (this.isStop || Math.abs(moveX) < 5) {
				return;
			}
			console.log("移动距离过小");
			if (Math.abs(moveY) > Math.abs(moveX)) {
				// 竖向滑动-不触发左滑效果
				this.isStop = true;
				return;
			}
			//向左滑动
			if (moveX < 0) {
				this.theIndex = index;
				this.isStop = true;
			}else if (moveX > 0) {
				if (this.theIndex != null && this.oldIndex == this.theIndex) {
					this.oldIndex = index;
					this.theIndex = null;
					this.isStop = true;
					setTimeout(() => {
						this.oldIndex = null;
					}, 150);
				}
			}
		},
		touchEnd(index, $event) {
			//结束禁止触发效果
			this.isStop = false;
		},
		//控制左滑删除效果-end

		//商品跳转
		toGoods(e) {
			uni.showLoading({
				title:'加载中'
			});
			uni.navigateTo({
				url: '../../goods/goods',
				success() {
					uni.hideLoading()
				}
			});
		},
		//跳转确认订单页面
		toConfirmation() {
			let tmpList = [];
			let len = this.goodsList.length;
			for (let i = 0; i < len; i++) {
				if (this.goodsList[i].selected) {
					tmpList.push(this.goodsList[i]);
				}
			}
			if (tmpList.length < 1) {
				uni.showToast({
					title: '请选择商品结算',
					icon: 'none'
				});
				return;
			}
			uni.setStorage({
				key: 'buylist',
				data: tmpList,
				success: () => {
					uni.navigateTo({
						url: '../../order/confirmation'
					});
				}
			});
		},
		//删除商品
		deleteGoods(id) {
			this.delShoppingCart(id)
		},
		// 删除商品s
		deleteList() {
			this.clearShoppingCart()
			this.sum()
		},
		// 选中商品
		selected(index) {
			this.goodsList[index].selected = this.goodsList[index].selected ? false : true;
			let i = this.selectedList.indexOf(this.goodsList[index].id);
			i > -1 ? this.selectedList.splice(i, 1) : this.selectedList.push(this.goodsList[index].id);
			this.isAllselected = this.selectedList.length == this.goodsList.length;
			this.sum();
		},
		//全选
		allSelect() {
			let len = this.goodsList.length;
			let arr = [];
			for (let i = 0; i < len; i++) {
				this.goodsList[i].selected = this.isAllselected ? false : true;
				arr.push(this.goodsList[i].id);
			}
			this.selectedList = this.isAllselected ? [] : arr;
			this.isAllselected = this.isAllselected || this.goodsList.length == 0 ? false : true;
			this.sum();
		},
		// 减少数量
		sub(index) {
			if (this.cart[index].number <= 1) {
				return;
			}
			this.cart[index].number--;
			this.sum();
		},
		// 增加数量
		add(index) {
			this.cart[index].number++;
			this.sum();
		},
		// 合计
		sum(e, index) {
			this.sumPrice = 0;
			let len = this.cart.length;
			for (let i = 0; i < len; i++) {
				if (this.cart[i].selected) {
					if (e && i == index) {
						this.sumPrice = this.sumPrice + e.detail.value * this.cart[i].price;
					} else {
						this.sumPrice = this.sumPrice + this.cart[i].number * this.cart[i].price;
					}
				}
			}
			this.sumPrice = this.sumPrice.toFixed(2);
		},
		discard() {
			//丢弃
		}
	}
};
